<template>
	<view class="page-container">
		<view class="top-notice">
			
		</view>
		<view class="form" abelPosition="left">
			<u--form v-model="formList" >
				<u-form-item label="请假类型" prop="formList.type"  labelWidth="200" @click="showType = true;">
					<u-input v-model="formList.type" border="none" placeholder="请选择请假类型"></u-input>
					<u-icon  slot="right" name="arrow-right"></u-icon>
				</u-form-item>
				<u-form-item label="预计出校时间" prop="formList.estimateStartTime" labelWidth="200" @click="showDate1 = true">
					<u-input v-model="formList.estimateStartTime" border="none"></u-input>
					<u-icon  slot="right" name="arrow-right"></u-icon>
				</u-form-item>
				<u-form-item label="预计返校时间" prop="formList.estimateEndTime"  labelWidth="200" @click="showDate2 = true">
					<u-input v-model="formList.estimateEndTime" border="none"></u-input>
					<u-icon  slot="right" name="arrow-right"></u-icon>
				</u-form-item>
				<u-form-item label="请假去向" prop="formList.type"  labelWidth="200"　@click="showArea = true">
					<u-input v-model="formList.target" border="none"></u-input>
					<u-icon  slot="right" name="arrow-right"></u-icon>
				</u-form-item>
				<u-form-item label="请假原因" prop="formList.message"  labelWidth="200">
					<u-input v-model="formList.message" border="none"></u-input>
				</u-form-item>
			</u--form>
			<u-action-sheet
							:show="showType"
							:actions="actions"
							title="请选择请假类型"
							@close="showType = false"
							@select="TypeSelect"
					>
					</u-action-sheet>
			<u-picker :show="showArea" ref="uPicker" :columns="columns" keyName="name" @confirm="confirm" @cancel="showArea = false" @change="changeHandler"></u-picker>
			<u-datetime-picker
			                :show="showDate1"
											@confirm="getStartTime"
											@cancel="showDate1 = false"
											:formatter="formatter"
			                mode="time"
			        ></u-datetime-picker>	
			<u-datetime-picker
			                :show="showDate2"
											:minDate="1640966400000"
											@confirm="getEndTime"
											@cancel="showDate2 = false"
			                mode="datetime"
			        ></u-datetime-picker>					
		</view>
		<view class="btn">
			<u-button type="primary" @click="submitLeave">提交</u-button>
		</view>
	</view>
</template>

<script>
	import {GetProvinceList, GetCityListByCode, GetCountyListByCode} from '../../../api/system.js'
	import {dateFormat} from '../../../utils/date.js'
	export default {
		data() {
			return {
				showType: false,
				actions: [{name: '事假', value: 0},{name: '病假', value: 1}],
				showArea: false,
				columns: [],
				showDate1: false,
				showDate2: false, 
				value1: Number(new Date()),
				formList: {
					type: null,
					estimateStartTime:null,
					estimateEndTime: null,
					target:null,
					message: null
				}
			}
		},
		created() {
			this.getProviceList()
		},
		methods: {
			TypeSelect(e) {
				this.formList.type = e.value
			},
			confirm(e) {
				console.log(e);
				this.formList.target = `${e.value[0].name}${e.value[1].name}${e.value[2].name}`
				this.showArea = false
			},
			formatter(type, value) {
				console.log(type);
					if (type === 'hour') {
					    return `${value}时`
					}
					if (type === 'minute') {
					    return `${value}分`
					}
			        return value
			    },
			getStartTime(e) {
				this.formList.estimateStartTime = dateFormat(null, e.value, 1)
				this.showDate1 = false
			},
			getEndTime(e) {
				this.formList.estimateEndTime = dateFormat( e.value,null, 0)
				this.showDate2 = false
			},
			changeHandler(e) {
				const {columnIndex, value, values, index, picker = this.$refs.uPicker} = e
				switch(columnIndex) {
					case 0:
							this.getCityList(value[0].code)
							break
					case 1:
							this.getCountyList(value[1].code)
				}
			},
			getProviceList() {
				GetProvinceList().then((res) => {
					this.$refs.uPicker.setColumnValues(0, res)
				})
			},
			getCityList(code) {
				const data = {provinceCode: code}
				GetCityListByCode(data).then((res) => {
					this.$refs.uPicker.setColumnValues(1, res)
				})
			},
			getCountyList(code) {
				const data = {cityCode: code}
				GetCountyListByCode(data).then((res) => {
					this.$refs.uPicker.setColumnValues(2, res)
				})
			},
			submitLeave() {
				
			}
		}
	}
</script>

<style lang="less" scoped>
	.page-container {
		width: 100vw;
		height: 95vh;
		background-color: #fafafa;
		display: flex;
		flex-direction: column;
		.top-notice {
			height: 30%;
			background-color: aqua;
			margin-bottom: 1vh;
		}
		.form {
			flex: 1;
			.u-form {
				.u-form-item {
					padding: 0 1vw;
					height: 50px;
					margin-bottom: 1vh;
					background-color: #fff;
				}
			}
		}
		.btn {
			flex-basis: 10%;
			padding: 0 10vw;
		}
	}
</style>